import React, { useEffect, useState } from "react";
import axios from "axios";
//Material UI
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Drawer from "@mui/material/Drawer";
import Divider from "@mui/material/Divider";
//components
import SideList from "./SideList";
import Detail from "./Detail";

const drawerWidth = 430;

export default function Sidebar({
  setGetDataForMarkers,
  MakrerId,
  clickDetail,
  setClickDetail,
  isMarker,
  setIsMarker,
}) {
  // shopList Data
  const [shopList, setShopList] = useState(null);
  // loading & error hooks
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  // to get detail

  const [getId, setGetId] = useState(null);

  const shopListURL = "http://sulrae.com/api/shop/";
  //get shopList Data
  useEffect(() => {
    console.log("axios1");
    const fetchShopList = async () => {
      try {
        setError(null);
        setShopList(null);
        setLoading(true);
        const response = await axios.get(shopListURL);
        setShopList(response.data);
        setGetDataForMarkers(response.data);
      } catch (e) {
        setError(e);
      }
      setLoading(false);
    };

    fetchShopList();
  }, [setGetDataForMarkers]);

  if (loading) return <div>Ïû†ÏãúÎßå Í∏∞Îã§Î¶¨Í∞úüê∂</div>;
  if (error) return <div>Error</div>;
  if (!shopList)
    return <div>Ìï¥ÎãπÏßÄÏó≠ÏóêÎäî ÏïÑÏßÅ Îì±Î°ùÎêú Î∞òÎ†§ÎèôÎ¨º ÌóàÏö©Ïπ¥ÌéòÍ∞Ä ÏóÜÎã§Í∞úüê∂ </div>;

  return (
    <Drawer
      variant="permanent"
      sx={{
        width: drawerWidth,
        flexShrink: 0,
        [`& .MuiDrawer-paper`]: {
          width: drawerWidth,
          boxSizing: "content-box",
        },
      }}
    >
      <Toolbar />
      {/* <Box sx={{ overflow: "auto" }}> */}
      <Box>
        {/* Ïª®ÌÖêÏ∏† ÎüâÏóê Îî∞Îùº Ïä§ÌÅ¨Î°§Î∞îÎ•º Ï∂îÍ∞ÄÌï†ÏßÄ ÏûêÎèôÏúºÎ°ú Í≤∞Ï†ï*/}
        {clickDetail ? (
          <Detail
            getId={getId}
            setClickDetail={setClickDetail}
            MakrerId={MakrerId}
            isMarker={isMarker}
            setIsMarker={setIsMarker}
          />
        ) : (
          <SideList
            setGetId={setGetId}
            shopList={shopList}
            setClickDetail={setClickDetail}
          />
        )}
        <Divider />
      </Box>
    </Drawer>
  );
}
