import React from "react";
import { RenderAfterNavermapsLoaded, NaverMap, Marker } from "react-naver-maps";

export default function NaverApiMap({
  getDataForMarkers,
  setMakrerId,
  setClickDetail,
  setIsMarker,
}) {
  const navermaps = window.naver.maps;

  return (
    <div>
      <RenderAfterNavermapsLoaded clientId={"cc08dar8r4"}>
        <NaverMap
          id={"map"}
          mapDivId={"react-naver-map"}
          style={{
            width: "100%",
            height: "850px",
          }}
          defaultCenter={
            new navermaps.LatLng(37.2840915640963, 127.012674868847)
          }
          defaultZoom={16}
        >
          {getDataForMarkers?.map((marker) => (
            <Marker
              key={marker.shop_id}
              onClick={() => {
                setMakrerId(marker.shop_id);
                setIsMarker(true);
                setClickDetail(true);
              }}
              position={new navermaps.LatLng(marker.latitude, marker.longitude)}
              animation={0}
              icon={{
                url: "https://i.ibb.co/JBMG7b9/marker.png",
                size: { width: 40, height: 46 },
                scaledSize: { width: 40, height: 46 },
                anchor: { x: 12, y: 32 },
              }}
            />
          ))}
        </NaverMap>
      </RenderAfterNavermapsLoaded>
    </div>
  );
}

