import styled from "styled-components";

//Ï†ÑÏ≤¥
export const Container = styled.div`
  @import url("https://fonts.googleapis.com/css2?family=Jua&display=swap");
  font-family: "Jua", sans-serif;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  .Detail_bar {
    margin: 7px;

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    /* border-bottom: 2px solid lightgray; */
    .head_message {
      margin-top: 5px;
      font-size: 21px;
      display: flex;
      width: 330px;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
  }
`;
// ÏÉÅÎã®
export const Button = styled.button`
  @import url("https://fonts.googleapis.com/css2?family=Jua&display=swap");
  font-family: "Jua", sans-serif;
  padding: 6px 12px;
  color: black;
  font-size: 16px;
  border: none;
  width: 70px;
  height: 30px;
  border-radius: 4px;
  background-color: #ffe680;
  :hover {
    background-color: #ff9966;
    color: white;
  }
`;

export const DetailInfoDiv = styled.div`
  padding: 10px;
  .barHeadName {
    font-size: 25px;
  }
`;

export const InstagramLink = styled.a`
  :link {
    color: tomato;
    text-decoration: none;
  }
  :visited {
    color: tomato;
  }
  :hover {
    color: pink;
  }
`;

//for menu
export const MenuKind = styled.span`
  font-size: 21px;
  .Menu {
    padding: 5px;
    border-radius: 5px;
    background-color: lightgray;
  }
`;
export const PhotoImg = styled.img`
  width: 100%;
  height: 250px;
  border-radius: 1.5%;
`;

export const Ul = styled.li`
  list-style: none;
  padding-left: 15px;
`;
